<template>
  <div class="holder text-center">
    <div class="md-layout md-alignment-center">
      <div class="md-layout-item md-size-50 md-small-size-100">
        <h1 class="mar-b-4">{{$t('checkout.thankYou')}}</h1>
        <h2 class="mar-b-2">
          {{$t('checkout.orderNumber')}}:
          {{orderId}}
          {{$t('checkout.confirmText')}}
        </h2>
        <div class="mar-b-2">
          <md-button
            @click="onClick()"
            class="md-button_opacity"
          >
            <div class="md-layout">
              <div class="md-layout-item mar-r-1 arrow">&lt;</div>
              <div class="md-layout-item">{{$t('checkout.goToMain')}}</div>
            </div>
          </md-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex';

export default {
  name: 'Final',
  data: () => ({
    orderId: localStorage.getItem('orderId'),
  }),
  methods: {
    ...mapActions(['deleteCartItem']),
    onClick() {
      localStorage.removeItem('orderId');
      this.$router.push('/');
      this.deleteCartItem();
    },
  },
};
</script>
